import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { Store } from '@ngrx/store';
import { AppState, DataState} from '../../store/state';
import { AppActions } from '../../store/actions/appActions';
import { Router } from '@angular/router';

@Component({
  selector: 'app-businesses',
  templateUrl: './businesses.component.html',
  styleUrls: ['./businesses.component.scss']
})
export class BusinessesComponent implements OnInit {

  businesses: FirebaseListObservable<any[]>;
  category: string = 'Business';
  query: string = '';
  school: string;
  constructor(public db: AngularFireDatabase, private store: Store<AppState>, private router: Router) { }

  ngOnInit():void {
    this.store.select((state: AppState) => {
        return state;
    }).subscribe((state: AppState) => {
      this.businesses = state.data.campusEntities;
      this.school = state.view.school;
    });
  }

  searchUpdate(value: string): void {
    this.query = value.toLowerCase();
  }

  qualify(entity: any): boolean {
    if(entity.category == this.category){
      if((entity.name.toLowerCase()).indexOf(this.query) != -1){
        return true;
      } else if((entity.type.toLowerCase()).indexOf(this.query) != -1) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  edit(org: any): void {
    org.school = this.school;
    this.store.dispatch({type: AppActions.SET_VIEW_ORGANIZATION, payload: org});
    this.router.navigate(['/vieworganization']);
  }
}


