import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { Store } from '@ngrx/store';
import { AppState, DataState } from '../../store/state';
import { AppActions } from '../../store/actions/appActions';
import { Router } from '@angular/router';

@Component({
  selector: 'app-grub',
  templateUrl: './grub.component.html',
  styleUrls: ['./grub.component.scss']
})
export class GrubComponent implements OnInit {

  grubs: FirebaseListObservable<any[]>;
  category: string = 'Grub';
  query: string = '';
  city: string;
  constructor(public db: AngularFireDatabase, private store: Store<AppState>, private router: Router) { }

  ngOnInit():void {
    this.store.select((state: AppState) => {
        return state;
    }).subscribe((state: AppState) => {
        this.grubs = state.data.cityEntities;
        this.city = state.view.city;
    });
  }

  searchUpdate(value: string): void {
    this.query = value.toLowerCase();
  }

  qualify(entity: any): boolean {
    if(entity.category == this.category){
      if((entity.name.toLowerCase()).indexOf(this.query) != -1){
        return true;
      } else if((entity.type.toLowerCase()).indexOf(this.query) != -1) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  edit(business: any): void {
    business.city = this.city;
    this.store.dispatch({type: AppActions.SET_VIEW_BUSINESS, payload: business});
    this.router.navigate(['/viewbusiness']);
  }
}


