import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppState, ViewState} from '../store/state';
import { AppActions } from '../store/actions/appActions';
import { Router } from '@angular/router';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.scss']
})
export class AdminComponent implements OnInit {

  constructor(private store: Store<AppState>, private router: Router) { }

  service: string;
  city: string;
  school: string;

  ngOnInit():void {
  	this.store.select((state: AppState) => {
        return state.view;
    }).subscribe((view: ViewState) => {
      this.service = view.service;
      this.school = view.school;
      this.city = view.city;
    });
    this.store.dispatch({type: AppActions.GET_CITY_ENTITIES, payload: this.city});
  }

  changeService(service: string): void {
    if(service == 'CampusLive'){
      this.router.navigate(['admin/campus']);
      this.store.dispatch({type: AppActions.GET_CAMPUS_ENTITIES, payload: {city: this.getCity(this.school), school: this.school}});
    } else {
      this.router.navigate(['admin/experiences']);
      this.store.dispatch({type: AppActions.GET_CITY_ENTITIES, payload: this.city});
    }
    this.store.dispatch({type: AppActions.CHANGE_SERVICE, payload: service});
  }

  changeSchool(school: string): void {
    this.store.dispatch({type: AppActions.CHANGE_SCHOOL, payload: school});
    this.store.dispatch({type: AppActions.GET_CAMPUS_ENTITIES, payload: {city: this.getCity(school), school: school}});
  }

  changeCity(city: string): void {
    this.store.dispatch({type: AppActions.CHANGE_CITY, payload: city});
    this.store.dispatch({type: AppActions.GET_CITY_ENTITIES, payload: city});
  }

  logout(): void {
    this.store.dispatch({type: AppActions.LOGOUT, payload: null});
    this.router.navigate(['login']);
  }

  getCity(school: string): string {
    if(school == 'UC San Diego') {
      return 'San Diego';
    } else if(school == 'UC Santa Barbara'){
      return 'Santa Barbara';
    } else if(school == 'UC Davis'){
      return 'Davis';
    }
  }
}


